import 'reflect-metadata'
import { injectable, Container } from 'inversify'


@injectable()
class Katana {
    public hit() {
        return "cut!";
    }
}

@injectable()
class Ninja implements Ninja {
    public katana: Katana;
    public constructor(katana: Katana) {
        this.katana = katana;
    }
    public fight() { return this.katana.hit(); }
}


let container = new Container();

describe('Inversify resolve', ()=>{

	beforeEach(()=>{
		container = new Container()	
	})

	it('Inversify resolve root class without bind', ()=>{
		container.bind(Katana).toSelf();
		const ninja = container.resolve(Ninja);
		expect(ninja.fight()).toEqual("cut!");
	}, 10000)

	it('Inversify dosent`t register automatically sub dependencies', ()=>{
		//Ninja has been registered, but need to bind Katana too.
		container.bind(Ninja).toSelf()
		const tryGet = ()=>container.get(Ninja);
		expect(tryGet).toThrow("No matching bindings found for serviceIdentifier: Katana");
	})
})

